import { useState } from 'react';
import { Button, Dropdown, Nav, Offcanvas } from 'react-bootstrap'
import { Link, NavLink } from 'react-router-dom'
import './Header.css'


const Header = () => {

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <div className='header'>
      {window.screen.width < 600 ?
        <>
          <Button variant="primary" onClick={handleShow}>
            
          </Button>
          <Offcanvas show={show} onHide={handleClose}>
            <Offcanvas.Header closeButton>
              <Offcanvas.Title></Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <Dropdown.Item onClick={() => handleClose()} as={Link} to='/numbers'>O </Dropdown.Item>
              <Dropdown.Item onClick={() => handleClose()} as={Link} to='/yesorno'>  </Dropdown.Item>
              <Dropdown.Item onClick={() => handleClose()} as={Link} to='/moleInHole'>MoleInHole</Dropdown.Item>
              <Dropdown.Item onClick={() => handleClose()} as={Link} to='/calculator'></Dropdown.Item>
              <Dropdown.Item onClick={() => handleClose()} as={Link} to='/photos'></Dropdown.Item>
            </Offcanvas.Body>
          </Offcanvas>
        </>
        :
        <Nav fill variant="tabs" defaultActiveKey="/home">
          <Nav.Item>
            <Nav.Link as={NavLink} to='/numbers'>O </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={NavLink} to='/yesorno'>  </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={NavLink} to='/moleInHole'>MoleInHole</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={NavLink} to='/calculator'></Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={NavLink} to='/photos'></Nav.Link>
          </Nav.Item>
        </Nav>
      }
    </div>
  )
}

export default Header